import uniDrawer from "@/components/uni-ui/uni-drawer/uni-drawer.vue"
import card from "@/components/common/card.vue"
import zmradioGroup from "@/components/common/radio-group.vue"
import price from "../../components/common/price.vue"
export default {
	data() {
		return {
			// 下页url
			nextPage:"",
			// 排序
			paiList:[
				"click_num",
				"sold_num",
				"shop_price"
			],
			ordering:"is_index",
			// 搜索页面传过来的关键字
			keyword:"",
			// 是否显示右侧抽屉
			showRigth:false,
			// 商品列表
			list:[],
			screen:{
				currentindex:0,
				list:[
					{"name":"综合","status":1,"key":""},
					{"name":"销量","status":0,"key":""},
					{"name":"价格","status":0,"key":""}
				]
			},
			lable:{
				selected:0,
				list:[
					{"name":"选项一"},
					{"name":"选项二"},
					{"name":"选项三"}
				]
			},
			dataId:"",
			isColum:true
		}
	},
	onLoad(e) {
		this.keyword = e.keyword
		if(e.id){
			this.dataId = e.id
		}
		this.getData()
		
	},
	// 上拉加载更多
	onReachBottom(){
		// 上拉加载
	},
	methods: {
		changeCloum(){
			this.isColum = !this.isColum
		},
		zuzhiData(res){
			// {
			// 	title:"无线蓝牙耳机",
			// 	titlepic:"../../static/images/demo/cate_03.png",
			// 	desc:"雅致简约/雅致简约/雅致简约/雅致简约/雅致简约",
			// 	pprice:100,
			// 	comment_num:1300,
			// 	good_num:"98%"
			// }
			res.map(v=>{
				this.list.push({
						title:v.name,
						titlepic:v.goods_front_image,
						desc:v.goods_brief,
						pprice:100,
						comment_num:1300,
						good_num:"98%"
				})
			})
		},
		// 组织数据
		getData(callback){
			this.list = []
			console.log(this.ordering)
			this.$R.get("/goods/goodslist/?page_size=6",{ordering:this.ordering,id:this.dataId}).then(res=>{
				this.zuzhiData(res.results)
				this.nextPage = res.next
				if(typeof callback ==="function"){
					callback()
				}
			})
		},

		// 显示抽屉
		showDrawer(){
			this.showRigth = true
			// 记录旧的值
			let old = this.lable.selected
		},
		changeScrren(index){
			// this.currentindex = index
			let oldindex = this.screen.currentindex
			let olditem = this.screen.list[oldindex]
			if(oldindex === index){
				if(olditem.status ===1){
					olditem.status = 2
					 this.ordering ="-"+this.paiList[oldindex]
				}else{
					olditem.status = 1
					this.ordering = this.paiList[oldindex]
				}
				return this.getData()
			}
			let newitem = this.screen.list[index]
			// 移除旧激活状态
			olditem.status =0
			this.screen.currentindex =index
			this.ordering = this.paiList[index]
			// 增加新的状态
			newitem.status = 1
			// 再次加载数据
			this.getData()
		},
		confirm(){
		// 筛选获取数据
			this.getData()
			// 关闭抽屉
			this.showRigth = false
		}
	},
	onReachBottom() {
		if(this.nextPage!=="null" && this.nextPage){
			this.$R.request({url:this.nextPage,isurl:"url"}).then(res=>{
				this.nextPage = res.next
				this.zuzhiData(res.results)
			})
			
		}
	},
	onPullDownRefresh() {
		this.getData(()=>{
			uni.stopPullDownRefresh()
		})
		
	},
	computed:{
		options(){
			let obj = this.screen.list[this.screen.currentindex]
			let value = obj.status==1?"desc":"all"
			// 请求时把options 加入请求参数中
			return {
				[obj.key]:value
			}
		},
		price(){
			return true
		}
	},
	components:{
		uniDrawer,
		card,
		zmradioGroup,
		price
	}
}
